import * as ActionTypes from '../constants/actionTypes';
import * as Logger from '../constants/logCSS';

const initState = {
    isInit: false,
    witnesses: null,
    hearings: null,
    scheduledWitnesses: null
};

function mainReducer(state = initState, action) {
    let current = Object.assign({}, state);
    switch (action.type) {
        case ActionTypes.INIT_APP:
            if (action.hearingsData.hearings === undefined) {
                Logger.cl('NO HEARINGS SCHEDULED', Logger.WARNING);
                current.hearings = new Map();
            } else {
                let tmpHearings = new Map(action.hearingsData.hearings);
                current.hearings = new Map();
                for (const hearing of tmpHearings.entries()) {
                    let preppedHearing = hearing[1];
                    preppedHearing.themes = new Map(preppedHearing.themes);
                    current.hearings.set(hearing[0], preppedHearing);
                }
            }
            current.isInit = true;
            current.isDev = action.isDev;
            Logger.cl('EDITOR INITIALIZED', Logger.SUCCESS);
            break;
        default:
    }
    return current;
}

export default mainReducer;