import React from 'react';
import { connect } from 'react-redux';
import AnimateHeight from 'react-animate-height';

class Month extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      display: true
    };
  }

  handleMainDisplay() {
    this.setState({
      display: ! this.state.display
    });
  }
  
  render() {
    let mainHeight = 0;
    let iconDirection = <i className="fas fa-chevron-down"></i>
    if (this.state.display) {
      mainHeight = 'auto';
      iconDirection = <i className="fas fa-chevron-up"></i>
    }
    let hearingLinks = [];
    for (const hearing of this.props.hearings) {
      let transcriptLink = '';
      if (this.props.isDev) {
        transcriptLink = '/dataDev/transcripts/'
      } else {
        transcriptLink = '/data/transcripts/'
      }
      if (hearing.transcriptLink !== undefined && hearing.transcriptLink !== '') {
        hearingLinks.push(<p key={hearing.timeStamp} className="ArchiveVideoTitle">{new Date(hearing.timeStamp).format('F d')} - <a href={transcriptLink + hearing.transcriptLink} className="ArchiveVideoLink">Transcript</a></p>);
      }
    }
    return (
      <div className="WebArchiveMonthDiv" onClick={this.handleMainDisplay.bind(this)}>
        <h3 style={{float: 'right'}}>{iconDirection}</h3>
        <h3>{this.props.name}</h3>
        <AnimateHeight duration={500} height={mainHeight}>
          {hearingLinks}
        </AnimateHeight>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isDev: state.isDev
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Month);