export const ERROR = 'font-weight: bold; color: red';
export const WARNING = 'font-weight: bold; color: orange';
export const INFO = 'font-weight: bold; color: blue';
export const SUCCESS = 'font-weight: bold; color: green';
export const GENERAL = 'font-weight: bold; color: grey';

export function cl(msg, css) {
    console.log('%c' + msg, css);
}